.button {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  width: 230px;
  height: 50px;
  border: 2px solid #E9E9E9;
  background-color: transparent;
  border-radius: 10px;
  color: #E9E9E9;
  font-size: 18px;
  margin-top: 80px;
}

.button:last-of-type {
  margin-top: 20px;
}